@font-face {
  font-family: "Cairo";
  src: url("../fonts/cairo-regular.woff2") format("woff2"), url("../fonts/cairo-regular.woff") format("woff"), url("../fonts/Cairo-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Cairo";
  src: url("../fonts/cairo-medium.woff2") format("woff2"), url("../fonts/cairo-medium.woff") format("woff"), url("../fonts/cairo-medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

body {
  margin: 0;
}

section,
aside,
nav,
footer,
main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 700;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

a,
button {
  -webkit-tap-highlight-color: transparent;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
  -webkit-tap-highlight-color: transparent;
  outline: 0;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 0;
}

fieldset {
  padding: 0;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  font-family: var(--body-font);
  font-size: var(--body-font-size-mobile);
  line-height: var(--body-line-height-mobile);
  top: 0;
  right: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  min-width: 360px;
  margin: 0;
  padding: 0;
  color: var(--body-color);
  background: var(--body-background);
  position: relative;
}
@media screen and (min-width: 681px) {
  body {
    font-size: var(--body-font-size);
    line-height: var(--body-line-height);
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: 0;
}

svg {
  max-height: 100%;
}

select:not([multiple]) {
  vertical-align: middle;
}

table {
  width: 100%;
  max-width: 100%;
  border-spacing: 0;
  border-collapse: separate;
}

th {
  font-weight: var(--table-header-font-weight);
  text-align: left;
}

th,
td {
  line-height: inherit;
  padding: var(--table-cell-padding);
}

th {
  vertical-align: bottom;
}

td {
  vertical-align: top;
}

a {
  text-decoration: none;
  color: var(--violet-500);
  outline: 0;
}
a:hover {
  color: var(--violet-500);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: var(--heading-font-weight);
  line-height: var(--heading-line-height);
  margin: var(--heading-margin);
}
h1:first-child, h2:first-child, h3:first-child, h4:first-child, h5:first-child, h6:first-child {
  margin-top: 0;
}

p {
  margin: var(--paragraph-margin);
}
p:first-child {
  margin-top: 0;
}
p:last-child {
  margin-bottom: 0;
}

dl,
ol,
ul {
  margin: var(--list-margin);
}
dl:first-child,
ol:first-child,
ul:first-child {
  margin-top: 0;
}
dl:last-child,
ol:last-child,
ul:last-child {
  margin-bottom: 0;
}

ol {
  list-style: decimal;
}

hr {
  display: block;
  height: 1px;
  margin: var(--hr-size);
  border: 0;
  background: var(--gray-100);
}

::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.hidden-box {
  display: none;
  visibility: hidden;
}

iframe {
  border: 0;
  outline: 0;
}

.videoWrapper {
  position: relative;
  height: 0;
  padding-bottom: 56.25%; /* 16:9 */
}

.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.text-center {
  text-align: center;
}

header,
nav,
section {
  display: block;
}

@keyframes dropdown-display {
  0% {
    opacity: 0;
    transform: scale(0.98) translateY(-0.6em);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
@keyframes dropdown-display-up {
  0% {
    opacity: 0;
    transform: scale(0.98) translateY(0.6em);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
blockquote {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  font-style: italic;
  position: relative;
  padding-left: 20px;
  color: var(--gray-900, #101828);
}
@media screen and (min-width: 1081px) {
  blockquote {
    margin: 48px 0;
  }
}
blockquote:after {
  content: "";
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 0;
  width: 3px;
  background: var(--primary-700, #6941C6);
}

[data-x-cloak] {
  display: none;
}

mark {
  padding: 2px 3px;
  border-radius: 2px;
}

@keyframes mmslideIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes mmslideOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes mmslideInBg {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.7;
  }
}
@keyframes mmslideOutBg {
  from {
    opacity: 0.7;
  }
  to {
    opacity: 0;
  }
}
.modal {
  display: none;
}

.modal.is-open {
  display: block;
}

.modal[aria-hidden=false] .modal__overlay {
  animation: mmslideIn 0.3s linear;
}

.modal[aria-hidden=true] .modal__overlay {
  animation: mmslideOut 0.3s linear;
}
.modal__overlay {
  position: fixed;
  z-index: 8888;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  overflow-y: auto;
  align-items: normal;
  flex-flow: row nowrap;
  justify-content: normal;
  box-sizing: border-box;
  padding: 8px;
  background: rgba(0, 0, 0, 0.6);
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
}
.modal__overlay _:-ms-fullscreen, :root .modal__overlay {
  display: block;
}
@media screen and (min-width: 681px) {
  .modal__overlay {
    padding: 20px;
  }
}
.admin-bar .modal__overlay {
  padding-top: 40px;
}
@media screen and (max-width: 680px) {
  .modal__overlay--news {
    padding: 0;
  }
}
.overflowscrolling .modal__overlay {
  overflow-y: scroll;
}

.modal__container {
  position: relative;
  z-index: 20;
  box-sizing: border-box;
  max-width: 1408px;
  width: 100%;
  margin: auto;
  padding: 80px 20px 20px;
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: var(--base-white);
}
.modal__container--thank {
  max-width: 398px;
}
@media screen and (min-width: 681px) {
  .modal__container {
    padding: 40px;
  }
}

.modal__close {
  display: flex;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 12px;
  position: absolute;
  right: 20px;
  top: 20px;
  background: var(--gray-iron-900, #30323B);
  border: none;
  outline: none;
}
@media screen and (min-width: 681px) {
  .modal__close {
    width: 48px;
    height: 48px;
    top: 40px;
    right: 40px;
  }
}

.modal__bg {
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  cursor: pointer;
}

.modal__title {
  font-size: 36px;
  font-weight: 500;
  line-height: 42px;
  margin-bottom: 24px;
}
@media screen and (min-width: 681px) {
  .modal__title {
    font-size: 64px;
    font-weight: 500;
    line-height: 72px;
    margin-bottom: 32px;
  }
}

.modal__desc-title {
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  margin-bottom: 24px;
}
@media screen and (min-width: 681px) {
  .modal__desc-title {
    font-size: 28px;
    font-weight: 500;
    line-height: 40px;
    max-width: 445px;
  }
}

@media screen and (min-width: 1081px) {
  .modal__desc {
    max-width: 700px;
    padding-right: 50px;
  }
}

.modal__subtitle {
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  margin-bottom: 32px;
  margin-top: 40px;
}
@media screen and (min-width: 681px) {
  .modal__subtitle {
    font-size: 36px;
    font-weight: 500;
    line-height: 42px;
  }
}

.modal-block {
  margin-bottom: 32px;
}
.modal-block:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 1081px) {
  .modal__part-top {
    display: flex;
    justify-content: space-between;
    gap: 40px;
  }
}

.modal-block__icon {
  display: flex;
  width: 48px;
  height: 48px;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  background: var(--brand-500, #3C648F);
  margin-bottom: 24px;
}
@media screen and (min-width: 681px) {
  .modal-block__icon {
    width: 64px;
    height: 64px;
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 681px) {
  .modal__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px 64px;
  }
}
@media screen and (min-width: 1081px) {
  .modal__list {
    grid-template-columns: repeat(4, 1fr);
  }
}

.modal-block__title {
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  margin-bottom: 12px;
}
@media screen and (min-width: 681px) {
  .modal-block__title {
    font-size: 28px;
    font-weight: 500;
    line-height: 40px;
  }
}

.modal__unit {
  margin-top: 40px;
  padding: 16px;
  border-radius: 24px;
  background: var(--gray-iron-50, #E9ECF0);
}
@media screen and (min-width: 681px) {
  .modal__unit {
    padding: 20px;
  }
}
@media screen and (min-width: 1081px) {
  .modal__unit {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.modal__unit-name {
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  margin-bottom: 12px;
}
@media screen and (min-width: 681px) {
  .modal__unit-name {
    font-size: 36px;
    font-weight: 500;
    line-height: 42px;
    margin-bottom: 24px;
  }
}

.modal__unit-text {
  margin-bottom: 24px;
}
@media screen and (min-width: 681px) {
  .modal__unit-text {
    margin-bottom: 32px;
  }
}
@media screen and (min-width: 1081px) {
  .modal__unit-text {
    margin-bottom: 0;
    max-width: 642px;
  }
}

.modal__mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.b-btn-hide {
  display: inline-block;
  opacity: 0;
  visibility: hidden;
  width: 0;
  height: 0;
  overflow: hidden;
}

.cookies {
  display: none;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 100;
  max-height: 100svh;
  overflow-y: auto;
}
.cookies.is-show {
  display: block;
}
.cookies.is-active .cookies__content {
  display: block;
}
.cookies.is-active .cookies__buttons {
  display: none;
}
@media screen and (min-width: 1081px) {
  .cookies {
    max-width: 536px;
    left: auto;
    right: 0;
  }
  .cookies.is-active {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
    max-width: 100%;
    left: 50%;
    right: auto;
    top: 50%;
    bottom: auto;
    transform: translateX(-50%) translateY(-50%);
  }
  .cookies.is-active:after {
    content: "";
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(3px);
  }
}

.cookies__wrapper {
  width: 100%;
  padding: 24px 16px;
  border-top: 1px solid var(--base-white-10, rgba(255, 255, 255, 0.1));
  background: var(--purple-900, #170B3A);
  position: relative;
  z-index: 3;
}
@media screen and (min-width: 681px) {
  .cookies__wrapper {
    padding: 20px;
    overflow: hidden;
  }
}
@media screen and (min-width: 1081px) {
  .cookies__wrapper {
    max-width: 536px;
    border-radius: 16px;
  }
}

.cookies__close {
  position: absolute;
  width: 20px;
  height: 20px;
  right: 12px;
  top: 12px;
  cursor: pointer;
}

.cookies__name {
  color: var(--base-white, #FFF);
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  text-transform: uppercase;
  margin-bottom: 7px;
}

.cookies__desc {
  color: var(--gray-blue-500, #9E9AA9);
  font-size: 14px;
  font-weight: 500;
  line-height: 22px;
}

.cookies__buttons {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  align-items: center;
}
@media screen and (min-width: 681px) {
  .cookies__buttons {
    flex-direction: row;
    gap: 12px;
  }
}

.cookies__btn {
  width: 100%;
}
@media screen and (min-width: 681px) {
  .cookies__btn {
    width: auto;
  }
}

@media screen and (min-width: 681px) {
  .cookies__button-table {
    margin-left: auto;
    padding: 0;
  }
}

.cookies__content {
  display: none;
  padding-top: 12px;
}

.cookies__list {
  margin-top: 24px;
}

.cookies__item {
  border-radius: 8px;
  border: 1px solid var(--base-white-10, rgba(255, 255, 255, 0.1));
  background: var(--purple-900, #170B3A);
  padding: 12px;
  margin-bottom: 8px;
  color: var(--base-White, #FFF);
  font-size: 14px;
  font-weight: 500;
  line-height: 22px;
}

.cookies__item-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cookies__item-name {
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
}
.cookies__item-name svg {
  transition: transform 0.35s;
}
.cookies__item-name.is-active svg {
  transform: rotate(45deg);
