  display: none;
  color: var(--gray-blue-500, #9E9AA9);
  padding-top: 12px;
}
.cookies__data.is-active {
  display: block;
  animation: 0.5s opacity-display;
}

.cookies__link {
  color: var(--violet-500, #7B6EE2);
